import React from 'react';
import './styles/App.css';
import './styles/index.css';
import { Box, Typography, Link } from '@mui/material';

export default function App() {
    return (
        <Box className="app">
            <Typography className='heading' variant="h1" sx={{ marginBottom: '20px' }}>
                We've Upgraded!
            </Typography>
            <Typography variant="body1" align='center' sx={{ marginBottom: '30px' }}>
                We've moved to a new, more memorable address to enhance your experience.
            </Typography>
            <Typography variant="h6" align='center' sx={{ marginBottom: '20px' }}>
                Visit {''}
                <Link
                    href="https://duel.tools"
                    target="_blank"
                    rel="noopener noreferrer"
                    sx={{
                        fontSize: '24px',
                        color: 'primary.main',
                        '&:hover': {
                            color: 'primary.dark',
                            textDecoration: 'underline',
                        },
                    }}
                >
                    duel.tools
                </Link>
                {''} to continue using this tool.
            </Typography>
            <Typography variant="body2" align='center' sx={{ marginTop: '30px' }}>
                Thank you for your continued support. We look forward to serving you better at our new site!
            </Typography>
        </Box>
    );
}